import { Component, OnInit } from '@angular/core';
import { ActivatedRouteSnapshot, Router, RouterStateSnapshot, UrlTree } from '@angular/router';
import { VhAlgorithm, VhQuerySales, VhAuth } from 'ionic-vhframeworks';
import { Observable } from 'rxjs'; 
import { ReceiveMoneyA4Component } from 'src/app/sales/components/print/receive-money/receive-money-a4/receive-money-a4.component';
import { ReceiveMoneyA5Component } from 'src/app/sales/components/print/receive-money/receive-money-a5/receive-money-a5.component';
import { ReceiveMoneyK57Col3Component } from 'src/app/sales/components/print/receive-money/receive-money-k57-col3/receive-money-k57-col3.component';
import { ReceiveMoneyK80Col3Component } from 'src/app/sales/components/print/receive-money/receive-money-k80-col3/receive-money-k80-col3.component';
import { ReceiveMoneyK80Col4Component } from 'src/app/sales/components/print/receive-money/receive-money-k80-col4/receive-money-k80-col4.component';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-detail',
  templateUrl: './detail.component.html',
  styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {

  constructor(
    private router: Router,
    public vhAlgorithm: VhAlgorithm,
    private vhQuerySales: VhQuerySales,
    private vhAuth: VhAuth,
    private vhComponent: VhComponent,
    public lang: LanguageService,
  ) {
  }
  checkPrinter: boolean = false
  dataRestore: any = this.router.getCurrentNavigation().extras.state.dataRestore
  billDetail: any = this.router.getCurrentNavigation().extras.state.billDetail
  nzOptions: any;
  ngOnInit(): void {
    let printer = this.vhQuerySales.getlocalPrinters().find(item => item.desktop)
    if (printer && printer.desktop.delivery_receive_money) this.checkPrinter = printer.desktop.delivery_receive_money[printer.desktop.delivery_receive_money.type].enable;
    this.getPaymentBankList();
  }
  /**Hàm tạo phiếu chi */
  /**back */
  goBack() {
    this.router.navigate(['/sales/dashboard/manage/cashflow/receiving'], { state: this.dataRestore });
  }
  /**Get payment bank list */
  payment_method: Array<any> = []

  
  /**Get tk ngân hàng và gán váo biến listPaymentBank
   * @example this.getPaymentBankList()
  */
  getPaymentBankList() {
    let listPayment = this.vhQuerySales.getlocalWallets().filter(item => item._id != 'id_cash' && item.id_branch == this.vhQuerySales.getDefaultBranch()._id);
    let listPaymentBank = listPayment.map(item => {
      return { value: item._id, label: item.name, isLeaf: true }
    })
    let options = [{ value: '1', label: this.lang.translate('Cash'), isLeaf: true }, { value: '3', label: this.lang.translate('Account'), children: listPaymentBank }]
    this.nzOptions = options;
  }

  /**
   * kiểm tra thông tin máy in và mở modal in của đơn này
   * @param goback :  biến cờ để check có quay về trang trước hay ko
   * @example this.showPrint(true)
   */
  showPrint(goback?) {
    this.checkPrint().then((printer) => {
      if (printer) {
        this.vhComponent.showModal(this.renderPrintPage(printer['_id']), {
          billInfo: {
            ...this.billDetail,
            id_branch_send: this.billDetail['id_branch'],
            id_employee: this.vhAuth.getUser()._id
          },
          payment_type: +this.payment_method[0],
          id_wallet: +this.payment_method[0] == 1 ? 'id_cash' : this.payment_method[1],
          printer: printer
        }, false, false, `modal-print-${printer['_id']}`)
          .then(modal => {
            modal.onWillDismiss().then(dataReturn => { if (goback) this.goBack() })
          })
      }
    }, err => { this.goBack() })
  }

  /**
   * trả về component của trang in tương ứng
   * @example this.renderPrintPage('print_size_a4')
   * @returns component
   */
  private renderPrintPage(type: 'print_size_a4' | 'print_size_a5' | 'print_size_k80_4c' | 'print_size_k80_3c' | 'print_size_k57_3c') {
     
    switch (type) {
      case 'print_size_k57_3c': return ReceiveMoneyK57Col3Component
      case 'print_size_k80_3c': return ReceiveMoneyK80Col3Component
      case 'print_size_k80_4c': return ReceiveMoneyK80Col4Component
      case 'print_size_a5': return ReceiveMoneyA5Component
      case 'print_size_a4': return ReceiveMoneyA4Component
      default: return ReceiveMoneyA4Component
    }
  }
  /*----------Print------------------*/
   /**
   * lấy thông tin dữ liệu trang in
   * @returns Promise<any>
   * @example this.checkPrint()
   */
  checkPrint() {
    return new Promise((resolve, rejects) => {
      let printer = this.vhQuerySales.getLocalPrintPage('page_desktop_delivery_receive_money')
      if (printer  && printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)].enable) resolve(printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)])
      else rejects("No")
    })
  }

  /**
   * nhận từ sk nhấn button 'xác nhận' ở giao diện, cập nhật lại hóa đơn này thành đơn đa nhận bill-type 34 và tạo bill với bill-type 3234
   * @param print : biến cờ để check in hay ko
   * @example this.createBill(true)
   * 
   */
  createBill(print) {
    this.vhComponent.alertConfirm("", this.lang.translate("Confirm"), this.lang.translate("Receive money") + "?", "Ok", this.lang.translate("Cancel")).then(() => {
      this.vhComponent.showLoading("", "transparent-loading").then(() => {
        this.vhQuerySales.updateBill(this.billDetail['_id'], this.createInvoice("3334")).then((res) => {
          this.vhComponent.hideLoading(0).then(() => {
            this.vhComponent.showToast(2000, this.lang.translate("Receive money successfully"), "success-toast")
            if (print) this.showPrint(true)
            else this.goBack()
          })
        }).catch(err => { this.vhComponent.hideLoading(0); console.error(err) })
      })
    }).catch(() => { })
  }

  /**
   * trả về các trường của bill có bill-type 3234
   * @example let bill = this.createInvoice('3234')
   */
  createInvoice(bill_type) {
    let invoice: any = {
      bill_code: this.billDetail['bill_code'],
      total: this.billDetail['total'],
      payment: this.billDetail['payment'],
      content: this.billDetail['content'],
      payment_type: +this.payment_method[0],
      id_wallet: +this.payment_method[0] == 1 ? 'id_cash' : this.payment_method[1],
      bill_type: bill_type,
      id_branch: this.billDetail['id_branch_receive'],
      id_branch_send: this.billDetail['id_branch'],
      id_employee: this.vhAuth.getUser()._id,
      date: this.billDetail['date']
    }
    return invoice
  }
}
